.mainblock {
    margin-right: 30px;
    margin-left: 10px;
    font-family: "Comic Sans MS", cursive, sans-serif;
}

.section {
    background-color: #3a3939;
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    margin-bottom: 30px;
}

.leftSubSection {
    flex: 1;
    min-height: 350px;
    height: 100%;
    align-items: flex-end;
    align-content: flex-end;
    padding-top: 1%;
}

.section sectionHeader {
    color: white;
    margin-top: 0;
    margin-bottom: 0;
    background-color: #3a3939;
}

.section h1 {
    color: white;
    margin-left: 100px;
    margin-top: 0;
    margin-bottom: 0;
}

.section h1:hover {
    color: #e4e1e1;
}

.subSection {
    display: flex;
    flex-direction: row;
    flex: 1;
}

.rightSubSection {
    flex: 2;
    padding-top: 10;
    padding-bottom: 10;
}

.rightSubSection p {
    color: white;
    padding-left: 5px;
}

.rightSubSection h2 {
    color: white;
    font-size: 15px;
}

.section img {
    align-self: center;
}